












































import Vue from 'vue'
// @ts-ignore
import VueHeadful from 'vue-headful'
import { mapState } from 'vuex'

// Components
import BaseConflictInfo from '@/components/BaseConflictInfo'
import BaseEventInfo from '@/components/BaseEventInfo'
import BaseEventsChain from '@/components/BaseEventsChain'

import {
    isNaturalNumber,
    titleEnhancer,
} from '@/utils'

// Route names
import { HOME_ROUTE_NAME } from '@/router/route-names'

// Store
import { GET_EVENT } from '@/store/modules/events/getter-types'
import { READ_EVENT } from '@/store/modules/events/action-types'


export default Vue.extend({
    name: 'EventDetails',

    components: {
        BaseConflictInfo,
        BaseEventInfo,
        BaseEventsChain,
        VueHeadful,
    },

    computed: {
        ...mapState({
            // @ts-ignore
            isLoaded: state => state.isLoaded,
            // @ts-ignore
            isLoading: state => state.isLoading,
        }),

        event(): any {
            return this.$store.getters[GET_EVENT](
                Number(this.$store.state.route.params.id),
            )
        },

        currentRouteTitle(): string {
            return titleEnhancer(
                // @ts-ignore
                this.isLoaded ? `${this.event.title}` : '...',
            )
        },
    },

    beforeRouteEnter(to, from, next) {
        if (!isNaturalNumber(Number(to.params.id))) {
            next({
                name: HOME_ROUTE_NAME,
                replace: true,
            })
        }
        //      (this)    ,
        //       
        else {
            next(async (vm) => {
                try {
                    await vm.$store.dispatch(
                        READ_EVENT,
                        {
                            id: vm.$store.state.route.params.id,
                        },
                    )
                }

                //   ,      ,
                //     .
                // TODO         
                catch (error) {
                    vm.$router.replace({
                        name: HOME_ROUTE_NAME,
                    })
                }
            })
        }
    },

    async beforeRouteUpdate(to, from, next) {
        //  `id`  - "" 
        if (!isNaturalNumber(Number(to.params.id))) {
            next(false)
        }
        else {
            //         `id`   ,
            //    
            try {
                await this.$store.dispatch(
                    READ_EVENT,
                    {
                        id: to.params.id,
                    },
                )
                next({
                    params: {
                        id: to.params.id,
                    },
                })
            }

            //         .
            // `next(false)`   ,      `from`,
            //      
            catch (error) {
                // TODO      ? , ....
                next(false)
            }
        }
    },

})
